}
.dull-text{
    font-size: 21px;
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 40px;
    letter-spacing: -.3px;
    color: #686b78;
}


/*  HSEC3  */
.hsec3{
    margin-top:35px;
    width: 100%;
}
.input-container{
    display: flex;
    justify-content: space-between;
}
.inputPlocate{
    display: block;
    position: relative;
    width: 96%;
}
.input-box{
    padding: 0px;
    padding-left: 4%;
    width: 96%;
    height:58px;
    line-height: 28px!important;
    caret-color: #fc8019;
    font-size: 17px!important;
    font-weight: 600;
    border: 1px solid #bebfc5;
    border-right: transparent;
    outline: none;
    background-color: transparent;
}
.input-box:focus{
    border: 1px solid #fc8019;
    border-right: transparent;
    box-shadow: 0px 0 2px #888888;
}
.locate-btn{
    position: absolute;
    right: 7px;
    top: 10px;
    padding: 10px;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    letter-spacing: -.35px;
    background-color: transparent;
    border: none;
}
.locate-btn:hover{
    background-color:#F0F0F0 ;
}
.find-food-btn{
    color: #fff;
    background-color: #fc8019;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 12%);
    right: 0;
    height: 60px;
    line-height: 60px;
    width: 180px;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: .53px;
    padding: 0;
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    border: none;
}


/* HSEC4 */
.hsec4-h3{
    font-weight: 500;
    font-size: 15px;
    color: #a9abb2;
    margin: 30px 0 10px;
    letter-spacing: -.2px;
    text-transform: uppercase;
}
.list-para{
    /* max-width: 570px; */
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 14px;
}
.list-content-hsec4{
    display: inline-block;
}
.list-item-hsec4{
    font-weight: 600;
    margin-right: 8px;
    color: #686b78;
    line-height: 1.5;
    letter-spacing: -.3px;
    text-decoration: none;
}
.list-content-hsec4:nth-child(2n)  .list-item-hsec4{
    color: #93959f;
}



/* MediaQuery */
@media only screen and (max-width: 1300px) {
    .herosection{
        background-image:linear-gradient(rgba(255, 255, 255, 0.6),rgba(255,255, 255, 0.7)), url(../../static/media/Lun.d231d68a.jpeg);
        background-size:660px 540px;
        text-shadow: 0px 0px 1px #fff;
    }
    .hero-subsec{
        padding-left: 20px;
        max-width: 630px;
        align-items: center;
    }
}

@media only screen and (max-width: 500px) {
    .hero-subsec{
        padding-right: 20px;
    }
    .input-box::-webkit-input-placeholder{
        opacity: 0;
    }
    .input-box:-ms-input-placeholder{
        opacity: 0;
    }
    .input-box::placeholder{
        opacity: 0;
    }
    .heading-hsec2{
        font-size:30px;
    }
}


/* End Of The Section */
/* Features */
.features{
    background: #2b1e16;
    padding: 0 20px;
}
.features-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 80px;
    max-width: 1200px;
    margin: 0 auto;
}


/* Features Section */
.fesec{
    color: #fff;
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
}


/* Image Part */
.fesec-img{
    height: 250px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    
}
.fesec-imgtag{
    margin-bottom: 42px;
}


/* Heading Part */
.fesec-heading{
    font-size: 17px;
    font-weight: 600;
    margin-top: 0;
}


/* Text Part */
.fesec-text{
    font-size: 13px;
    width: 260px;
    margin-top: 10px;
    color: #e0cbc1;
    line-height: 1.5;
    letter-spacing: 0.1;
}


/* Media Query */
@media only screen and (max-width: 850px){
    .features-container{
        justify-content:center;
    }
}

/* End Of This Section */
.restaurants{
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    min-height: 570px;
}
.rsec1{
    padding-left: 20px;
    max-width: 400px;
}
.rsec2{
    display: flex;
    flex-wrap: wrap;
    max-width: 800px;
    height: inherit;
}
.img-rsec1{
    max-height: 489px;
    margin-bottom: 81px;
    padding:0;
}
.img-rsec2{
    max-height: 489px;
    margin-top: 81px;
}
.rsec-h1{
    margin-top: 0;
    margin-bottom: 0;
    font-size: 34px;
    font-weight: 600;
    width: 300px;
    line-height: 1.5;
}
.rsec-p{
    font-size: 17px;
    font-weight: 500;
    padding-top: 20px;
    padding-bottom: 60px;
    color: #7e808c;
    line-height: 1.5;
    margin: 0;
}
.rsec-img2{
    display: flex;
    flex-wrap: wrap;
}
.rsec-images2{
    cursor: pointer;
    height: 54px;
}


/* Media Query */
@media only screen and (max-width: 1185px) {

    .restaurants{
        justify-content: center;
    }
    .rsec2{
        margin-top: 30px;
        justify-content: center;
    }
}

@media only screen and (max-width: 780px){
    .img-rsec1{
        max-height: 489px;
        margin-bottom: 15px;
        padding:0;
    }
    .img-rsec2{
        max-height: 489px;
        margin-top: 15px;
    }
}
@media only screen and (max-width: 400px){
    .rsec-images2{
        margin-top: 15px;
        margin-bottom: 15px;
        margin-right: 0px!important;
    }
    .rsec-img2{
        justify-content: center;
    }
}
.footer{
    background-color:black ;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left:20px ;
    padding-right: 20px;
}


/* FSEC1 section */
.fsec1{
    max-width: 1200px; 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid #4d4d4d;
    padding-bottom: 30px;
}

/* list Component */
.list{
    min-width: 200px;
    margin-top: 30px;
    margin-bottom: 10px;
    list-style: none;
    padding:0;
}
.list li{
    margin-top: 15px;
}
.space{
    margin-top: 63px;
}
.fsec1-img{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    width: 200px;
}
.fsec1-img-1{
    margin-bottom: 30px;
    transition: 0.5s all ease-in-out;
}
.fsec2-img-2{
    transition: 0.5s all ease-in-out;
}
.fsec1-img-1:hover{
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.fsec2-img-2:hover{
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.listhead{
    color: gray;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 30px;
}
.listitem{
    color: white;
    font-weight: 300;
    text-decoration: none;
    font-size: 15px;
}
li > a:hover{
    font-weight: 600;
}
/* Footer Section 3 */
.fsec3{
    padding-top: 30px;
    max-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
}
.logo_swiggy{
    height: 42px;
}
.logo_swiggy:hover{
    cursor:pointer;
}
.footer-copyright{
    color:white;
    text-align: center;
    font-size: 20px;
    letter-spacing: -.6px;
    font-weight: 300;
}
.social_list{
    min-width: 200px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.social-item{
    height: 24px;
    transition: 0.3s all ease-in-out;
}
.social-item:hover{
    cursor: pointer;
    height: 24px;
    -webkit-transform : scale(1.2);
            transform : scale(1.2);
}
@media only screen and (max-width: 450px){
    .footer{
        text-align: center;
    }
    .fsec1{
        display: block;
        margin-right: auto;
        margin-left: auto;
    }
    .fsec2{
        display: block;
        margin-right: auto;
        margin-left: auto;
    }
    .fsec3{
        display: block;
        margin-right: auto;
        margin-left: auto;
    }
    .fsec1-img{
        display: block;
        margin-right: auto;
        margin-left: auto;
    }
}
